<template>
 <div >
   <mu-appbar style="position:fixed"  :title="title" >
     <mu-icon-button icon="menu" slot="left" @click="toggle(true)" />
     <mu-icon-menu icon="account_box"  slot="right">
     
     
      
    </mu-icon-menu>
  </mu-appbar>
  
  <div  style="padding-top:66px;100%" justify="center" wrap="wrap" align='center'>
    <br>
    <div v-if="contentType==1">
     <mu-chip   class="demo-chip" v-for='k,index in photokeys' :key="index" @click="updateUrl(k['url'])">
        {{k['tag']}}
     </mu-chip>
     <mu-row>
      <mu-col width="100" tablet="33" desktop="20"  class="img-container"  v-for="tile, index in list" :key="index">
         <h3 >{{tile.title}} (共{{tile.page}}张) </h3>
         
         <img :src="'http://img1.mm131.com/pic/'+tile.id+'/1.jpg'" style="width:100%;height:100%;padding:5px;" @click="gotomm(tile)" lazy="loading">
      </mu-col>
     </mu-row>
    </div>
  
     <div v-if="contentType==2">
     <mu-chip   class="demo-chip" v-for='k,index in bookkeys' :key="index" @click="updateUrl(k)">
        {{k['tag']}}
     </mu-chip>

     <mu-row>
         <mu-col width="100" tablet="33" desktop="20" class="img-container"   v-for="tile, index in list" :key="index">
           
           <h5 style="">{{tile.title}} (共{{tile.pages}}页) </h5>
            
              <img :src="tile.images[0]"  lazy="loading" style="width:100%;padding:5px;" @click="gotobook(tile)">
           
       </mu-col>
     </mu-row>
    </div>
     <div v-if="contentType==3">
     <mu-chip   class="demo-chip" v-for='k,index in videokeys' :key="index" @click="updateUrl(k)">
         {{k['tag']}}
     </mu-chip>
      
     <mu-row>
      <mu-col width="33" tablet="25" desktop="20"  class="img-container"  @click.native="gotosvideo(tile)" v-for="tile, index in list" :key="index ">
         <p >{{tile.title}}</p>
         <img :src="tile.poster+'+240'" style="width:100%;height:100%;"  lazy="loading">
         
      </mu-col>
      </mu-row>
    </div>
     <div v-if="contentType==4">
     <mu-chip   class="demo-chip" v-for='k,index in livekeys' :key="index" @click="updateUrl(k)">
         {{k['tag']}}
     </mu-chip>
     </br>
     <mu-row>
      <mu-col width="50" tablet="33" desktop="20"   class="img-container" v-for="tile, index in list" :key="index" >
         <h5 >{{cate}}</h5>
         <img  :src="tile.src"   lazy="loading" style="width:100%;height:auto;" @click="gotoLiveVideo(tile)" >
      </mu-col>
     </mu-row>
    </div>
   </div>
  
   <mu-infinite-scroll :loading="loading" @load="loadMore"/>
    <mu-paper style="position:fixed;width:100%;bottom:0px;">
    <mu-bottom-nav :value="bottomNav"  @change="handleChange">
     <mu-bottom-nav-item value="1" title="Pictures" icon="photo"/>
     <mu-bottom-nav-item value="2" title="Books" icon="books"/>
      <mu-bottom-nav-item value="3" title="Movies" icon="ondemand_video"/>

      <mu-bottom-nav-item value="4" title="Lives" icon="videocam"/>

      
      
    </mu-bottom-nav>
  </mu-paper>
   <mu-drawer  mu-drawer :open="open" :docked="docked" @close="toggle()">
    <mu-appbar  title="看美女，看直播！" >
     
    </mu-appbar>

  
    <mu-menu  @itemClick="toggle()">
      <mu-menu-item v-if="logined" title="我的" leftIcon="account_box" @click.native="gotoProfile()"/>
      <mu-menu-item v-if="logined" title="收藏" leftIcon="favorite"/>
      <mu-divider/>
     <!--  <mu-menu-item title="美图" @click.native="itemclick(1)" leftIcon="photo_album"/>
      <mu-menu-item title="漫画" @click.native="itemclick(2)" leftIcon="book"/>
      <mu-menu-item title="影视" @click.native="itemclick(3)" leftIcon="video_library"/>
      <mu-menu-item title="直播" @click.native="itemclick(4)" leftIcon="videocam"/>
      <mu-divider /> -->
      <mu-menu-item title="设置" leftIcon="settings"/>
      <mu-divider />
     <!--  <mu-menu-item title="帮助" leftIcon="help_outline"/> -->
      <mu-divider />
      <mu-menu-item v-if="!logined"  @click.native="tologin()" title="登陆" leftIcon="perm_identity"/>
      <mu-menu-item v-if="logined"  @click.native="loginout()" title="退出" leftIcon="power_settings_new"/>
    </mu-menu>
  </mu-drawer>
  <mu-dialog  :open="dialog" title="温馨提示">
    <h3>欢迎来到夜来香美女直播网，本网站为您提供优质的美女网络资源，包括且不限于图片，漫画，视频和直播，在网站测试期，游客可免费体验vip资源5分钟，免费注册用户可体验vip资源一个月，更多精彩请注册！{{this.$store.state.showinfo}}
    </h3>
    <mu-flat-button  label="关闭" slot="actions" primary @click="()=>{dialog=false;open=false}"/>
  </mu-dialog>
</div>
</template>

<script>
  //contentType 1 video   2 live  3book  4 pic
  import {mapActions} from 'vuex' 

  let Base64 = require('js-base64').Base64
  
  export default {
  	name:'home',

  	data() {
      return {
       dialog:false,
       canload:true,
       open:false,
       docked:true,
       loading:false,
       scroller: null,
       list:[],
       page:1,
       apitype:"movies",
       title:"Movies",
       contentType:3,
       screenWidth:0,
       bottomNav: '3',
      bottomNavColor: '1',
      apiurl:"/api/movies?tp=4K-STAR&page=",
      label:'Beautyleg',
      kw:'',
      cate:'性感',
     }
   
   },
   mounted() {
    
     this.scroller=window
     this.screenWidth=window.width
     this.dialog=this.$store.state.showTip
     
     this.loadMore()

   },
   computed:{
    msg(){
      return this.$store.state.test
    },
    logined(){
      return this.$store.state.loginedUser
    },
    livekeys(){
      return this.$store.state.livekeys
    },
    photokeys(){
      return this.$store.state.photokeys
    },
    bookkeys(){
      return this.$store.state.bookkeys
    },
    videokeys(){
      return this.$store.state.videokeys
    }
  },
  beforeRouteEnter(to,from,next)
    {
      next(vm=>{
        vm.canload=true
      })
    },
  beforeRouteLeave(to,from,next)
  { 
     if(to.path=="/login"){
       next(true)
       return 
    }
    if (to.path=="/picture"){
      next(true)
      return 
    }
    
    if(this.logined){
      next(true)
      this.canload=false
      return
    }

    if(this.canView()){
      next(true)
      this.canload=false
    }
    else {
       this.$router.push({path:"/login"})
       alert("您已经过了体验期，请注册！")
    }
   
   
},

methods: {

  canView() {
    if(!localStorage.getItem("canView")){
      return false
    }
    return localStorage.getItem("canView")=="true"
  }
  ,
  toggle (flag) {
    this.open=!this.open
    this.docked=!flag

  },
  handleChange (val) {
      this.bottomNav = val
      this.itemclick(val)
  },
  loadMore(added=true){
   
    if(!this.canload){
      return 
    }
    if(!added){
      this.list=[]
    }
    this.loading=true
    this.$http.get(this.$store.state.serverUrl+this.apiurl+this.page,{headers:{"Authorization":"test"}}).then(response=>{
     let result=JSON.parse(Base64.decode(response.body))

     if(added){
     
        this.list=this.list.concat(result)
      
    }
    else {
      this.list=result

    }
    this.loading=false
    this.page++
  },response=>{
    this.list=[]
  })
  },
  closeTip(){
    this.dialog=false
    this.open=false
  },
  gotobook(item){
   
    this.changebook(item)
    this.$router.push({path:'/book'})
  },
  gotoVipVideo(item)
  {
    this.changeVipVideo(item)
    this.$router.push({path:"/vipvideo"})
  },
  gotoProfile()
  {
    this.$router.push({path:"/profile"})
  },
  gotomm(item){
   let id=item.id
   let pages=item.page
   let pics=[]
   for (var i=1;i<=pages;i++){
    pics.push("http://img1.mm131.com/pic/"+id+"/"+i+".jpg")

  }
  this.changemm({title:item.title,images:pics})
  this.$router.push({path:'/picture'})
},
goto942Video(item)
{   
    let da={}
    da['poster']='https://www.942porn.com/media/videos/tmb/'+item.sid+"/1.jpg"
    let pics=[]
    for(var i =1;i<20;i++){
      pics.push('https://www.942porn.com/media/videos/tmb/'+item.sid+"/"+i+".jpg")
    }
    da['photos']=pics
    da['mp4']="https:"+item.url
    da['title']=item.title
    this.changevideo(da);
  this.$router.push({path:"/video"})

},
updateUrl(k)
{
  this.apiurl=k['url']
  this.cate=k['tag']
  this.page=1
  this.loadMore(false)  
},

updateCate(item)
{
  this.label=item
  this.page=1
  this.loadMore(false)
},
updateKeyWord(item,index)
{
 this.page=1
 this.kw=item
 this.cate=index
 this.loadMore(false)
},
gotoLiveVideo(item)
{ 
  
  this.changeLiveVideo(item)
  this.$router.push({path:"/livevideo"})
},
gotosvideo(item)
{
   item['photos']=[]
   this.changevideo(item);
   this.$router.push({path:"/video"})

},
gotovideo(item)
{ 

  if(item['poster'].indexOf("dmm.com")>0){
   let size=item['photonum']
   let pics=[]
   for (var i = 1; i <=size; i++) {
    pics.push({"url":"https://pics.dmm.com/digital/video/"+item['movienumber']+"/"+item['movienumber']+"jp-"+i+".jpg"})
  }
  item['poster']="https://pics.dmm.com/digital/video/"+item['movienumber']+"/"+item['movienumber']+"ps.jpg"
  item['photos']=pics
  
}
this.changevideo(item);
this.$router.push({path:"/video"})
},
...mapActions(['changevideo','changemm','changebook','changeVipVideo','changeLiveVideo','changeUser']),
refresh () {
  this.refreshing=true
  setTimeout(()=>{this.refreshing=false},5000)
},
tologin(){
  this.$router.push({path:"/login"})
},
loginout()
{
  localStorage.removeItem("user")
  localStorage.setItem('canView',false);
  this.changeUser(null)
  alert("您已成功推出当前账号")
  window.location="/"
},
itemclick(tp){
  this.list=[]
  if(tp==1){
    this.apiurl=this.photokeys[0]['url']
    this.title="Pictures"
    this.contentType=1;
  }
  else if(tp==2){
    this.apiurl=this.bookkeys[0]['url']
    this.title="Books"
    this.contentType=2;
  }
  else if(tp==3){
    this.apiurl=this.videokeys[0]['url']
    this.title="Movies"
    this.contentType=3;
  }
  else if(tp==4)
  {
    this.apiurl=this.livekeys[0]['url']
    this.title="Lives"
    this.cate="中文视频"
    this.contentType=4;
  }
  
 
  this.page=1

  this.loadMore(false)
},
currentType(tp){
  return this.apitype.indexOf(tp)>0
}
}
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style>
  .fixed {
    position:fixed;
    top: 0;
  }
  .title{
    color: #FFFFFF;
  }
  .img-container{
    display: block;
    width: 100%;
    height: auto;
    color: white;
    text-align: center;
  }

  .gridlist-demo-container{
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
  }
  .demo-infinite-container{
    width: 256px;
    height: 300px;
    overflow: auto;
    -webkit-overflow-scrolling: touch;
    border: 1px solid #d9d9d9;

  }
  .gridlist-demo{
    width: 500px;
    height: 450px;
    overflow-y: auto;
  }



